import axios from 'axios';
import {
    parseCreationOptionsFromJSON,
    create,
    get,
    parseRequestOptionsFromJSON,
    supported,
    AuthenticationPublicKeyCredential,
  } from "@github/webauthn-json/browser-ponyfill";

const apiUrl = process.env.REACT_APP_API_URL;

const useApiCall = (cookies) => {
    const apiCall = axios.create({
        baseURL: apiUrl,
        headers: {
            Authorization: '',
        }
    });

    apiCall.interceptors.request.use(
        async (config) => {
            if (cookies && cookies.id != null && cookies.token != null) {
                config.headers.Authorization = `Bearer ${cookies.token}`;
            }
            return config;
        },
        (error) => {
            console.log("Issue:", error)
            return Promise.reject(error);
        }
    );

    return apiCall;
};

const handleSplash = async (apiCall, setUser, cookies) => {
    if (cookies && cookies.id != null && cookies.token != null) {
        apiCall.get(`/users/${cookies.id}`)
        .then((res) => {
            setUser(res.data);
        })
    }
}

const handleAuth = async (apiCall, setUser, setAuthError, registeredCredentials, username, setCookie) => {
    console.log("process started...")
    if (registeredCredentials.length > 0) {
        const cro = parseRequestOptionsFromJSON({
            publicKey: {
            challenge: "CCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCC",
            allowCredentials: registeredCredentials,
            userVerification: "discouraged",
            },
        });

        // apiCall.post(`/auth/login/`, { username: username, auth: get(cro) })
        get(cro)  // only for demo site
        .then((res) => {
            // setCookie(res.data);
            // setUser(res.data);
            setUser({  // only for demo site
                registrations: [res.toJSON()]
            })
            setAuthError("Verified passkey!");
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    } else {
        const cco = parseCreationOptionsFromJSON({
            publicKey: {
              challenge: "CCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCC",
              rp: { name: "calvinhklui.com" },
              user: {
                id: username,
                name: username,
                displayName: username,
              },
              pubKeyCredParams: [],
              excludeCredentials: registeredCredentials,
              authenticatorSelection: { userVerification: "discouraged" },
              extensions: {
                credProps: true,
              },
            },
        });

        // apiCall.post(`/auth/signup/`, { username: username, auth: create(cco) })
        create(cco)  // only for demo site
        .then((res) => {
            // setCookie(res.data);
            // setUser(res.data);
            setUser({  // only for demo site
                registrations: [res.toJSON()]
            })
            setAuthError("Registered passkey!")
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    }
};

const handleLogout = async (apiCall, setUser, setAuthError, cookies, removeCookie) => {
    setUser(null);  // only for demo site
    setAuthError("");  // only for demo site

    if (cookies && cookies.id && cookies.token) {
        apiCall.post(`/auth/logout/${cookies.id}`)
        .then((res) => {
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        });
    }
}

const handleDelete = async (apiCall, setUser, setAuthError, cookies, removeCookie) => {
    setUser(null);  // only for demo site
    setAuthError("");  // only for demo site

    if (cookies && cookies.id && cookies.token) {
        apiCall.delete(`/users/${cookies.id}`)
        .then((res) => {
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    }
}

export { useApiCall, handleSplash, handleAuth, handleLogout, handleDelete };