import React, { useContext, useRef } from "react"
import { GamesContext } from '../../context/GamesState'
import "../../styles/auth.scss"
import { handleAuth } from '../../utils/auth';

// only for demo site
import Experience from "../experience.js"
import "../../styles/experiences.scss"
import isIcon from "../../images/cmuInfoSys.png"

const Auth = props => {
  const {
    onDarkMode,
    user,
    setUser,
    authError,
    setAuthError
  } = useContext(GamesContext);

  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";
  const modeClassesExp = onDarkMode ? "dark-mode-background dark-mode-outline" : "light-mode-background light-mode-outline";  // only for demo site
  const usernameRef = useRef(null);

  const registeredCredentials = (user) => {
    if (user && user.registrations) {
      return user.registrations.map((reg) => ({
        id: reg.rawId,
        type: reg.type,
        authenticatorAttachment: reg.authenticatorAttachment,
        attestationObject: reg.response ? JSON.stringify(reg.response.attestationObject) : [],
        transportsOrHandle: reg.response ? (reg.response.transports ? reg.response.transports : reg.response.userHandle) : []
      }));
    } else {
      return [];
    }
  }

  const handleClick = (event) => {
    const username = usernameRef.current.value;
    handleAuth(props.apiCall, setUser, setAuthError, registeredCredentials(user), username, props.setCookie);
  }

  return (
    <section className={modeClasses}>
      <div className="container">
        <div className="auth-container">
          <div className="words-container">
              <h2 className="subtitle">
                {registeredCredentials(user).length > 0 ? "View Passkey" : "Register Passkey"}
              </h2>
          </div>

          <div>
            <label className={`auth-label ${modeClasses}`} htmlFor="username">Username</label>
            <input className={`auth-input ${modeClasses}`} type="text" id="username" name="username" ref={usernameRef}></input>

            {/* only for demo site */}
            <div className="contact-container">
            <button
              className={`auth-button ${modeClasses}`}
              onClick={handleClick}
              id="continueButton"
              value="continue"
            >
              <b>{registeredCredentials(user).length > 0 ? "Verify" : "Continue"}</b>
            </button>

            <span className={"contact-confirmation copied"}>
            <b>{authError}</b>
            </span>
            </div>
          </div>

          {/* Remove after demo site */}
          <br></br>
          <br></br>
          {registeredCredentials(user).length > 0 && <div className="experiences-container">
            <h5>Credential Info</h5>
            <div className={`experiences ${modeClassesExp}`}>
              <div className="experiences-table-container">
                <table>
                  <tbody>
                    <tr>
                      {registeredCredentials(user).map((registration, index) => {
                        console.log("TEST", registration)
                        return (
                          <td key={index}>
                            <Experience
                              orgURL="https://fidoalliance.org/"
                              orgImage={isIcon}
                              orgName={registration.type}
                              positionName={registration.authenticatorAttachment}
                              positionDetails={registration.id}
                              positionDuration={registration.transportsOrHandle}
                            />
                          </td>
                        )
                      })}
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>}

          
        </div>
      </div>
    </section>
  );
}

export default Auth;